import React from "react";
import { SimpleCell, Cell, Button, Div, Link, Avatar } from "@vkontakte/vkui";

const RouletteItem = ({ id, name, photoUrl, go, userUrl, amount }) => {
  return (
    <Cell
      before={
        photoUrl ? (
          <Link href={userUrl} target="_blank">
            <Avatar
              size={48}
              src={photoUrl}
              // src={getAvatarUrl('user_xyz')}
            />
          </Link>
        ) : (
          <Avatar src={"https://vk.com/images/camera_200.png"} />
        )
      }
      // before={
      //   <Div
      //     id={id}
      //     // onClick={() => openModalStickers(price, id)}
      //   >
      //     {photo}
      //   </Div>
      // }
      // after={
      //   <Link id={id} mode="tertiary">
      //     –∫—É–ø–∏—Ç—å
      //   </Link>
      // }
      description={
        <>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "start",
              marginTop: 5,
              marginLeft: 10,
              marginBottom: 5,
            }}
          >
            <Button
              size="m"
              // onClick={() => qiwiPay(price, id)}
              // onClick={go}
              // data-to={namePanelGame}
              style={{
                backgroundColor: "green",
                color: "white",
                borderRadius: "6px",
                width: "120px",
              }}
            >
              {amount} DurkaCoins
            </Button>
          </Div>
        </>
      }
    >
      <Div
        style={{
          marginLeft: 10,
        }}
      >
        {name}
      </Div>
    </Cell>
  );
};

export { RouletteItem };
